.cardWrapper {
    background: #01FFEB;
    background: -webkit-radial-gradient(center, #01FFEB, #010101);
    background: -moz-radial-gradient(center, #01FFEB, #010101);
    background: radial-gradient(ellipse at center, #01FFEB, #010101);
    height: 300px;
    width: 300px;
    margin: 20px;
    border: 4px solid;
    border-radius: 10px;
}

.imageWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
}

.profilePic {
    border-radius: 50%;
    border: 3px solid;
}

.nameWrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    color: white;
    font-size: 20px;
    font-weight: 800;
}

.name {
    margin-right: 5px;
}

.contactWrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
    color: white;
    font-size: 15px;
    font-weight: 500;
}

.email {
    margin-bottom: 5px;
}

@media screen and (max-width: 1024px) {
    .cardWrapper {
        width: 33%;
    }
}


@media screen and (max-width: 700px) {
    .cardWrapper {
        width: 50%;
    }
}

@media screen and (max-width: 450px) {
    .cardWrapper {
        width: 100%;
    }
}